import ReactDOM from 'react-dom/client';
import { Provider } from 'react-redux';
import { createHashRouter, RouterProvider } from 'react-router-dom';
import { store } from './app/store';
import ConfirmInformation from './components/ConfirmInformation';
import UserChoosePlan from './components/UserChoosePlan';
import UserForm from './components/UserForm';
import UserLogin from './components/UserLogin';
import Layout from './Layout';

const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);

const router = createHashRouter([
  {
    element: <Layout />,
    children: [{
      element: <UserLogin />,
      path: "/"
    },
    {
      element: <UserChoosePlan plans={[]} />,
      path: "/choose-plan"
    },
    {
      element: <UserForm />,
      path: '/user-informations'
    },
    {
      element: <ConfirmInformation />,
      path: '/confirm-informations'
    }

    ]
  }
]
);

root.render(
  <Provider store={store}>
    <RouterProvider router={router} />
  </Provider>
);
