import { ButtonHTMLAttributes, DetailedHTMLProps } from 'react';
import styles from './Button.module.css';


type ButtonPropsOnClick = {
    type: 'button',
    onClick: () => {}
}

type SubmitButtonPropsm = {
    type: 'submit',
}


interface ButtonProps extends Omit<DetailedHTMLProps<ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement>, 'className'> {
    className?: string;
}

export default function Button({ onClick, type = 'button', children, className = '', ...props }: ButtonProps) {
    return (
        <button
            className={styles["button-container"]}
            onClick={type !== 'submit' ? onClick : () => { }}
            type={type}>
            {children}
        </button>
    )
}